.message{

    width: 100%;
    padding: 1em;
    border: 1px solid black;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 2em;
    border-radius: 5px;
}
.success{
    color: #155724;
    background-color: #D4EDDA;
    border-color: #C3E6CB;
    transition-delay: 1.5s;
}
.error{
    color: #721C24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
}